import * as React from 'react';
import Engine from './Engine';
import {Button, OverlayTrigger, Tooltip} from 'react-bootstrap';

export interface ProgressProps {
    engine: Engine;
}

export default class Commands extends React.Component<ProgressProps, {}> {

  button1=()=>{
    this.props.engine.cmd_pillage();
  }

  button2=()=>{
    this.props.engine.cmd_conqure();
  }

  button3=()=>{
    this.props.engine.cmd_proseletize();
  }

  button4=()=>{
    this.props.engine.cmd_invest();
  }

  button5=()=>{
    this.props.engine.cmd_sell();
  }

  render() {


    let tooltip1 = (<Tooltip>{'Send your villagers to steal gold'}</Tooltip>)
    let tooltip2 = (<Tooltip>{'Send your villagers and leaders to conqure a nearby village'}</Tooltip>)
    let tooltip3 = (<Tooltip>{'Send your Preachers to gain population'}</Tooltip>)
    let tooltip4 = (<Tooltip>{'Instruct your traders to invest your gold'}</Tooltip>)
    let tooltip5 = (<Tooltip>{'Instruct your teacher to sell degrees'}</Tooltip>)

    return (
      <div>
        <OverlayTrigger overlay={tooltip1}><Button onClick={this.button1}>{'Pillage'}</Button></OverlayTrigger>
        <OverlayTrigger overlay={tooltip2}><Button onClick={this.button2}>{'Conqure'}</Button></OverlayTrigger>
        <OverlayTrigger overlay={tooltip3}><Button onClick={this.button3}>{'Proseletize'}</Button></OverlayTrigger>
        <OverlayTrigger overlay={tooltip4}><Button onClick={this.button4}>{'Invest'}</Button></OverlayTrigger>
        <OverlayTrigger overlay={tooltip5}><Button onClick={this.button5}>{'Sell Knowledge'}</Button></OverlayTrigger>
      </div>
    );
  }
}



