import * as React from 'react';
import Engine from './Engine';
import {Button} from 'react-bootstrap';

export interface ProgressProps {
    engine: Engine;
    which: number;
}

export default class Buildings extends React.Component<ProgressProps, {}> {

  buy(which2: number){
    this.props.engine.buy(this.props.which*3+which2);
  }

  render() {
    
    let fn = [
      this.buy.bind(this, 0),
      this.buy.bind(this, 1),
      this.buy.bind(this, 2),
    ];
    let which = this.props.which;
    //let state = this.props.engine.state;
    let bldgs = this.props.engine.state.buildings.slice(which *3,which *3+3);
    let btns = bldgs.map(function(blg, index){
      let cost = blg.cost();
      
      return <Button onClick={fn[index]} key={index}>{blg.name}-#{blg.count}-${cost}</Button>;
    });
    //console.log(btns);
    
    //let label = `Attract Villager (${state.population}/${state.totalPop})`;
    return (
      <div>
        {'Buildings'}
        <div style={{display:'flex', flexDirection:'column'}}>
          {btns}
        </div>
      </div>
    );
  }
}



